<template>
	<view>
		<view class="login_bg" :style="{backgroundImage: `url(${login_bg})`}">
			<view class="title">
				企业管理员登录
			</view>
		</view>
		<view class="login-box">
			<view class="item">
				<view class="label">
					<img src="../../static/username.png" alt="" srcset="" />
					<view class="title">
						账号
					</view>
				</view>
				<u--input v-model="userName" placeholder="请输入您的账号" border="bottom" clearable></u--input>
			</view>
			<view class="item">
				<view class="label">
					<img src="../../static/pass.png" alt="" srcset="" />
					<view class="title">
						密码
					</view>
				</view>
				<u--input v-model="password" type="password" placeholder="请输入密码" border="bottom" clearable></u--input>
			</view>
		<!-- 	<view class="item">
				<view class="label">
					<img src="../../static/ver_code.png" alt="" srcset="" />
					<view class="title">
						验证码
					</view>
				</view>
				<u--input placeholder="请输入验证码" border="bottom" clearable></u--input>
			</view> -->
			<view class="btn-box">
				<view class="primary-btn" @click="login">
					登录
				</view>
				<view class="plain-btn">
					注册
				</view>
			</view>
		</view>

	</view>
</template>

<script>
	import {
		login_bg
	} from '@/static/base64/index.js'
	export default {
		data() {
			return {
				login_bg,
				userName: '',
				password: ''
			};
		},
		methods: {
			login() {
				console.log(11);
				if(!this.userName) return uni.showToast({title: '请输入您的账号',icon: 'none'});
				if(!this.password) return uni.showToast({title: '请输入密码',icon: 'none'});
				uni.showLoading({
					title: '登录中',
					mask: true
				});
				uni.setStorageSync('user_info', {userName: this.userName, password: this.password});
				setTimeout(()=> {
					uni.switchTab({
						url: '/pages/index/index'
					});
				}, 2000)
				// uni.request({
				//     url: 'https://www.hdowms.com/WMS_SCSPSys/m/user/login',
				// 	method: 'POST',
				//     data: {
				//         number: this.userName,
				// 		password: this.password
				//     },
				//     success: (res) => {
				//         console.log('resres', res);
				// 		// uni.hideLoading()
				//     }
				// });
			}
		}
	}
</script>

<style lang="scss">
	.login_bg {
		width: 100vw;
		height: 696rpx;
		background-repeat: no-repeat;
		z-index: -1;
		background-size: cover;
		position: absolute;
		top: 0rpx;

		.title {
			position: absolute;
			bottom: 230rpx;
			left: 65rpx;
			font-family: PingFangSC, PingFang SC;
			font-weight: 500;
			font-size: 35rpx;
			color: #FFFFFF;
			line-height: 48rpx;
		}
	}

	.login-box {
		width: 750rpx;
		height: calc(100vh - 500rpx);
		background: #FFFFFF;
		border-radius: 46rpx 46rpx 0rpx 0rpx;
		margin-top: 494rpx;
		padding-top: 60rpx;
		.item {
			margin-left: 30rpx;
			margin-bottom: 46rpx;
			.label {
				display: flex;
				align-items: center;
				gap: 4rpx;
				margin-bottom: 16rpx;
				img {
					width: 48rpx;
					height: 48rpx;
				}
				.title {
					font-weight: 500;
					font-size: 31rpx;
					color: #1A1A1A;
					padding-bottom: 4rpx;
				}
			}
		}
		.btn-box {
			width: 100%;
			margin-top: 110rpx;
			box-sizing: border-box;
			padding: 0rpx 82rpx;
		}
	}
</style>